import {div, h4, table, tbody, td, th, thead, tr, p} from "../../elements";
import React from 'react';

export default class FaqPage extends React.Component {
  render() {
    return div({className: 'container'},
      h4({}, '   '),
      table({className: 'table'},
        thead({},
          tr({},
            th({}, ' '),
            th({}, '')
          )
        ),
        tbody({},
          tr({},
            td({}, 'Ctrl + Enter'),
            td({}, '  '),
          ),
          tr({},
            td({}, 'Ctrl + F'),
            td({}, '  '),
          ),
          tr({},
            td({}, 'Ctrl + Alt + L'),
            td({}, ' '),
          ),
        )
      )
    )
  }
}



